"use client";

import React from "react";

const education = [
  {
    degree: "B.Tech in Computer Science and Engineering",
    institution: "National Institute of Technology, Example City",
    year: "2018 - 2022",
  },
  {
    degree: "Higher Secondary (Science Stream)",
    institution: "ABC Senior Secondary School",
    year: "2016 - 2018",
  },
];

const EducationSection = () => {
  return (
    <section id="education" className="py-20 bg-background">
      <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8">
        <h2 className="text-4xl font-bold mb-12 text-center">Education</h2>
        <div className="max-w-2xl mx-auto space-y-6">
          {education.map((edu, idx) => (
            <div
              key={idx}
              className="p-6 bg-card rounded-lg border border-border hover:border-orange-500/50 transition-colors"
            >
              <h3 className="text-xl font-bold mb-2">{edu.degree}</h3>
              <p className="text-orange-500 font-semibold mb-1">{edu.institution}</p>
              <p className="text-muted-foreground">{edu.year}</p>
            </div>
          ))}
        </div>
      </div>
    </section>
  );
};

export default EducationSection;
